@import url(http://fonts.googleapis.com/earlyaccess/notosanskr.css);
@import url(http://fonts.googleapis.com/earlyaccess/nanumgothic.css);

#my-page body{
    background-color: #f4f4f4;
    font-family: 'Noto Sans KR';
}

#my-page #logoImg{
    width: 256px;
    margin: 1rem 0 0 2rem;
}

#my-page #logoImg+h2{
    float: right;
    font-size: 5rem;
    margin: 0;
    margin-top: 3rem;
    margin-right: 5rem;
}

#my-page #apply-condition{
    margin: 0 auto;
    width: 95%;
}

#my-page #apply-condition h2,#notice-table h2{
    font-size: 3rem;
    font-family: 'Nanum Gothic';
    font-weight: normal;
    margin: 1.5rem 0;
    padding: 0 0 0 0.25rem;
}

#my-page #apply-condition table{
    border-collapse: collapse;
    font-family: 'Noto Sans KR';
    -webkit-box-shadow: 0rem 0.25rem #d5d5d5;
            box-shadow: 0rem 0.25rem #d5d5d5;
}

#my-page #apply-condition td{
    border: 0.1px solid #555;
    font-size: 2rem;
    padding: 1rem 1.5rem;
    text-align: center;
}

#my-page #apply-condition thead td{
    background: rgba(44, 181, 115, 1);
    color: #fff;
}

#my-page #apply-condition table{
    width: 100%;
}

#my-page #apply-condition tbody td{
    font-size: 2rem;
    text-align: center;
    background-color: #fff;
}

#my-page #apply-condition tbody td:last-child{
    padding: 0;
    margin: 0;
}

#my-page #apply-condition tbody td button{
    margin: 0;
    font-size: 1.5rem;
    width: 80%;
    height: 4rem;
    border-style: none;
    border-bottom: 0.5rem solid #d5d5d5;
    border-right: 0.2rem solid #d5d5d5;
    font-weight: bold;
    background-color: #fcfcfc;
}

#my-page #apply-condition tbody td button:active{
    background-color: #fff;
    color: #555;
    border-bottom : 0.2rem solid #d5d5d5;
}

#my-page #notice-table{
    margin: 0 auto;
    width: 95%;
    -webkit-box-shadow: 0rem 0.25rem #d5d5d5;
            box-shadow: 0rem 0.25rem #d5d5d5;
    margin-bottom: 5rem;
}

#my-page #notice-table table{
    border-collapse: collapse;
    width: 100%;
}

#my-page #notice-table tr{
    width: 100%;
    height: auto;
}

#my-page #notice-table thead td{
    background-color: rgba(44, 181, 115, 1);
    color: #fff;
    font-size: 1.2rem;
    padding: 1rem 0.5rem;
    text-align: center;
    border: 0.01px solid #000;
}


#my-page #notice-table tbody td{
    background-color: #fff;
    color: #555;
    font-size: 1.2rem;
    padding: 1rem 0.5rem;
    text-align: center;
    border: 0.01px solid #000;
}
/*
#my-page #notice-table tbody td{
    background-color: #fff;
    font-size: 1.2rem;
    padding: 1rem 0.5rem;
}
*/

#my-page #notice-table td:nth-child(1){
    width: 10%;
}

#my-page #notice-table td:nth-child(2){
    width: 65%;
}

#my-page #notice-table tbody td:nth-child(2){
    text-align: left;
    padding-left: 28px;
    overflow-x: hidden;
}

#my-page #notice-table td:nth-child(3){
    width: 15%;
}

#my-page #notice-table td:nth-child(4){
    width: 10%;
}

#my-page #notice-table button{
    display: block;
    width: calc(1000% - 6px);
    padding: 0;
    margin: 0;
    padding: 0.5rem 0;
    font-size: 2rem;
    background-color: #fafafa;
    border: 0.1px solid #aaa;
    
}


