.button{
    border: 0.5px;
    background:-webkit-gradient(linear, left top, right top, from(rgba(44,181,115,0.85)), to(rgba(51,204,231,0.85)));
    background:-webkit-linear-gradient(left, rgba(44,181,115,0.85), rgba(51,204,231,0.85));
    background:-o-linear-gradient(left, rgba(44,181,115,0.85), rgba(51,204,231,0.85));
    background:linear-gradient(to right, rgba(44,181,115,0.85), rgba(51,204,231,0.85));
    width: 188px;
    height: 50px;
    font-size: 13px;
    color: white;
    margin: 20px;
}

.button:hover{
    cursor: pointer;
}




